.sign-in {
  background-image: url("../img/user-form/form-background.webp");
  background-size: contain;
  background-repeat: no-repeat;
  height: 548px;
  width: 584px;
  padding: 40px 40px 44px;

  @include vp-1199 {
    width: 483px;
  }

  @include vp-767 {
    background-image: none;
    width: 100%;
    padding: 30px 0 0 0;
    border-top: 2px solid $color-tomato;
    height: auto;
  }
}

.sign-in__tabs {
  overflow: visible;
  user-select: none;
}

.sign-in__nav {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin: 0 0 40px;

  @include vp-767 {
    justify-content: center;
    margin: 0 0 20px;
  }
}

.sign-in__button {
  font-family: $ff-montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0.625px;
  border: none;
  background-color: transparent;
  color: $color-default-black;
  padding: 0;
  margin: 0;
  user-select: none;

  @include vp-1199 {
    font-size: 20px;
  }

  &:hover,
  &focus,
  &:focus-visible {
    transform: scale(1.05);
    outline: none;
  }

  &:active {
    opacity: 0.5;
  }

  &:disabled {
    opacity: 0.2;
  }
}

.sign-in__button.is-active {
  color: $color-tomato;
}

.sign-in__item {
  background-image: url("../img//user-form/field-1.webp");
  border: none;
  background-size: 403px 50px;
  background-repeat: no-repeat;

  input {
    background-color: transparent;
    border: none;
    width: 403px;
    height: 50px;

    &:hover,
    &:focus,
    &:focus-visible {
      outline: none;

      &::placeholder {
        opacity: 0.5;
      }
    }

    &:disabled {
      opacity: 0.2;
    }

    &::placeholder {
      color: $color-default-black;
      font-family: $ff-montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 145.4%;
      letter-spacing: 0.5px;
      padding: 10px 10px 10px 0;

      @include vp-1199 {
        font-size: 16px;
      }
    }
  }

  @include vp-767 {
    background-size: 100% 50px;
  }

  &:hover,
  &:focus,
  &:focus-visible,
  &:active {
    outline: none;
    transform: scale(1.01);
  }

  &:disabled {
    opacity: 0.2;
  }
}

.sign-in__select {
  background-image: url("../img//user-form/field-1.webp");
  border: none;
  background-size: 403px 50px;
  background-repeat: no-repeat;

  input {
    background-color: transparent;
    border: none;
    width: 403px;
    height: 50px;

    &::placeholder {
      color: $color-default-black;
      font-family: $ff-montserrat;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 145.4%;
      letter-spacing: 0.5px;
      padding: 10px 10px 10px 0;

      @include vp-1199 {
        font-size: 16px;
      }
    }
  }

  @include vp-767 {
    background-size: 100% 50px;
  }
}

.sign-in__button-submit {
  font-family: $ff-montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: 0.45px;
  color: $color-default-white;
  text-align: center;
  border: none;
  background-color: transparent;
  background-image: url("../img/user-form/user-form-btn.webp");
  background-size: 403px 50px;
  width: 403px;
  min-height: 50px;
  cursor: pointer;

  @include vp-1199 {
    font-size: 14px;
  }

  @include vp-767 {
    background-size: 100% 100%;
    width: 100%;
  }

  &:hover,
  &:focus,
  &:focus-visible {
    outline: none;
    transform: scale(1.01);
  }

  &:active {
    color: $color-default-black;
    opacity: 0.9;
    outline: none;
  }

  &:disabled {
    opacity: 0.2;
  }
}
