.feedback {
  background-color: $color-default-white;
}

.feedback__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
  padding: 156px 0;

  @media (min-width: 1200px) and (max-width: 1439px) {
    padding: 100px 60px;
  }

  @include vp-1199 {
    padding: 50px 60px;
  }

  @include vp-767 {
    padding: 60px 15px 40px;
    gap: 30px;
  }
}

.feedback__title {
  text-align: center;
  margin: 0 0 60px;
  padding: 0;

  @include vp-1199 {
    margin: 0 0 40px;
  }

  @include vp-767 {
    margin: 0;
  }
}

.feedback__list {
  display: flex;
  flex-direction: row;
  gap: 30px;

  @include vp-767 {
    width: 100%;
    flex-direction: column;
  }
}

.feedback__card {
  width: 380px;

  @media (min-width: 1200px) and (max-width: 1439px) {
    width: 340px;
  }

  @include vp-1199 {
    width: 196px;
  }

  @include vp-767 {
    width: 100%;
    max-width: 100%;
  }

  &:hover,
  &:focus,
  &:focus-visible,
  &:active {
    outline: none;
    transform: scale(1.01);
  }

  p {
    font-family: $ff-montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.45px;
    margin: 0;
    padding: 0 0 15px;
    color: $color-blue-zodiac;

    @include vp-1199 {
      font-size: 14px;
    }
  }

  span {
    font-family: $ff-montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 145.4%;
    letter-spacing: 0.5px;
    padding: 0;
    margin: 0;

    @include vp-1199 {
      font-size: 14px;
    }
  }
}

.feedback__card-title {
  margin: 0;
  padding: 18px 0 5px;
  font-family: $ff-montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0.63px;

  @include vp-1199 {
    font-size: 18px;
  }

  @include vp-767 {
    padding: 5px 0;
  }
}
